const SHA256 = require('crypto-js/sha256') // Importe la fonction de hachage SHA256 de la bibliothque crypto-js
const dayjs = require('dayjs') // Importe la bibliothque dayjs pour la manipulation des dates
const Log = require('./log') // Importe le module Log personnalis pour la journalisation

let hashVisualiser = '' // Initialise la variable pour afficher le hachage dans l'interface graphique
const emptyFunction = () => {} // Fonction vide pour viter les erreurs

// Dfinit la classe Block
class Block {
  /**
   * Constructeur de la classe Block
   * @param {Object} data - Les donnes  stocker dans le bloc
   * @param {Date} date - La date de cration du bloc
   * @param {string} previousHash - Le hachage du bloc prcdent dans la chane
   */
  constructor(data, date, previousHash = '') {
    this.data = data // Donnes  stocker dans le bloc
    this.timestamp = dayjs(date).unix() // Convertit la date en timestamp UNIX avec dayjs
    this.hash = undefined // Initialise le hachage comme indfini
    this.previousHash = previousHash // Hachage du bloc prcdent
    this.hash = this.calculateHash() // Calcule le hachage du bloc
    this.nonce = 0 // Nonce utilis pour le minage
    this.transactions = [] // Initialise la liste des transactions
    this.options = {
      log: true // Option pour activer la journalisation
    }
  }

  /**
   * Calcule le hachage du bloc en utilisant SHA256
   * @returns {string} Le hachage du bloc
   */
  calculateHash() {
    const payload = JSON.stringify(this.data) // Convertit les donnes en chane de caractres
    const pattern = this.previousHash + this.timestamp + this.nonce + payload // Concatne previousHash, timestamp, nonce et donnes
    return SHA256(pattern).toString() // Retourne le hachage SHA256 du motif
  }

  /**
   * Mine le bloc en trouvant un hachage qui rpond aux critres de difficult
   * @param {number} difficulty - Le niveau de difficult du processus de minage
   * @returns {Promise} Une promesse qui se rsout lorsque le bloc est min avec succs
   */
  mine(difficulty = 2, transactions = []) {
    return new Promise((resolve, reject) => {
      const start = Date.now() // Enregistre l'heure de dbut
      const intervalId = setInterval(() => {
        const current = Date.now() // Obtient l'heure actuelle
        this.nonce++ // Incrmente le nonce
        document.getElementById("nonceResult") === null ? emptyFunction() : document.getElementById("nonceResult").innerText = this.nonce
        document.getElementById("nonceResult") === null ? emptyFunction() : document.getElementById("hashResult").innerText = this.hash
        this.hash = this.calculateHash() // Recalcule le hachage avec le nouveau nonce
        const currentTime = Math.floor((current - start) / 1000) // Calcule le temps coul en secondes
        if (this.options.log) console.log(`[Block] Minage du bloc - hachage gnr et trouv ${this.hash} | ${currentTime}s | nonce ${this.nonce}`) // Journalise la progression du minage
        document.getElementById("hashResult") === null ? emptyFunction() : document.getElementById("hashResult").innerText = this.hash
        if (this.hash.slice(0, difficulty) === '0'.repeat(difficulty)) { // Vrifie si le hachage rpond aux critres de difficult
          clearInterval(intervalId) // Arrte le minage
          const end = Date.now() // Enregistre l'heure de fin
          const time = (end - start) / 1000 // Calcule le temps total de minage
          this.transactions = transactions // Ajoute les transactions au bloc
          if (this.options.log) {
            console.log(`[Block] Minage du bloc - Succs en ${time}s avec nonce ${this.nonce}`) // Journalise le message de succs
            Log.add(`[Block] Minage du bloc - Succs en ${time}s avec nonce ${this.nonce}`) // Ajoute le message de succs au journal
          }
          resolve(this) // Rsout la promesse avec le bloc min
        }
      }, 10) // Dfinit l'intervalle  10 millisecondes
    })
  }
}

module.exports = {Block, hashVisualiser} // Exporte la classe Block et la variable hashVisualiser