const SHA256 = require('crypto-js/sha256'); // Importe la fonction de hachage SHA256 de la bibliothque crypto-js
const dayjs = require('dayjs'); // Importe la bibliothque dayjs pour la manipulation des dates
const { SignatureGenerator } = require('./signature'); // Importe le gnrateur de signature depuis le fichier signature.js

class Transaction {
    /**
     * Constructeur de la classe Transaction
     * @param {string} fromAddress - L'adresse de l'expditeur
     * @param {string} toAddress - L'adresse du destinataire
     * @param {number} amount - Le montant de la transaction
     * @param {Date} date - La date de la transaction, par dfaut  la date actuelle
     */
    constructor(fromAddress, toAddress, amount, date = dayjs()) {
        this.fromAddress = fromAddress; // Adresse de l'expditeur
        this.toAddress = toAddress; // Adresse du destinataire
        this.amount = parseInt(amount); // Montant de la transaction, converti en entier
        this.timestamp = dayjs(date).unix(); // Timestamp UNIX de la date de la transaction
    }

    /**
     * Calcule le hachage de la transaction
     * @returns {string} Le hachage de la transaction
     */
    calculateHash() {
        const pattern = this.fromAddress + this.toAddress + this.amount; // Cre un motif avec les adresses et le montant
        return SHA256(pattern).toString(); // Retourne le hachage SHA256 du motif
    }

    /**
     * Signe la transaction avec une cl prive
     * @param {string} signingKey - La cl prive pour signer la transaction
     * @returns {Transaction} L'instance de la transaction signe
     */
    sign(signingKey) {
        const signatureGenerator = (new SignatureGenerator(signingKey)).fromPrivate(signingKey); // Cre une instance de SignatureGenerator avec la cl prive
        if (signatureGenerator.getAddress() !== this.fromAddress) {
            throw new Error('Invalid signing key'); // Lance une erreur si la cl ne correspond pas  l'adresse de l'expditeur
        }
        this.signature = signatureGenerator.generate(this.calculateHash()); // Gnre la signature de la transaction
        return this; // Retourne l'instance de la transaction
    }

    isValid() {
        // Vrifie si l'adresse de l'expditeur et du destinataire sont prsentes
        if (!this.fromAddress || !this.toAddress) return false;
            
        // Vrifie si la signature est prsente
        if (!this.signature) return false;
        // Cre une instance de SignatureGenerator pour la cl publique de l'expditeur
        const signatureGenerator = (new SignatureGenerator).fromPublic(this.fromAddress);
        // Vrifie si la signature est valide en la comparant avec le hachage de la transaction
        if (!signatureGenerator.verify(this.calculateHash(), this.signature)) {
            // Si la signature n'est pas valide, affiche le hachage et la signature pour le dbogage
            console.log({
                hash: this.calculateHash(),
                signature: this.signature,
            });
            return false; // Retourne faux si la signature n'est pas valide
        }
        return true; // Retourne vrai si toutes les vrifications sont passes
    }
}

module.exports = Transaction

