const elliptic = require('elliptic') // Importe la bibliothque elliptic pour la cryptographie

// Dfinit la classe KeyGenerator pour gnrer des paires de cls
class KeyGenerator {
    // Gnre une paire de cls publique/prive
    generate() {
        const EC = new (elliptic.ec)('secp256k1') // Utilise la courbe elliptique secp256k1
        const key = EC.genKeyPair() // Gnre la paire de cls
        return {
            privateKey: key.getPrivate('hex'), // Renvoie la cl prive en hexadcimal
            publicKey: key.getPublic('hex'), // Renvoie la cl publique en hexadcimal
        }
    }
}

// Dfinit la classe SignatureGenerator pour gnrer et vrifier des signatures
class SignatureGenerator {
    constructor() {
        this.key = undefined // Initialise la cl  indfini
    }

    // Charge une cl prive pour la signature
    fromPrivate(privateKey) {
        this.key = (new (elliptic.ec)('secp256k1')).keyFromPrivate(privateKey, 'hex') // Charge la cl prive
        return this // Permet le chanage des mthodes
    }

    // Charge une cl publique pour la vrification
    fromPublic(publicKey) {
        this.key = (new (elliptic.ec)('secp256k1')).keyFromPublic(publicKey, 'hex') // Charge la cl publique
        return this // Permet le chanage des mthodes
    }

    // Renvoie l'adresse publique associe  la cl
    getAddress() {
        return (this.key) ? this.key.getPublic('hex') : undefined // Renvoie l'adresse publique en hexadcimal
    }

    // Gnre une signature pour un hachage donn
    generate(hash) {
        const signature = this.key.sign(hash, 'base64') // Signe le hachage
        return signature.toDER('hex') // Renvoie la signature en format DER hexadcimal
    }

    // Vrifie une signature pour un hachage donn
    verify(hash, signature) {
        return this.key.verify(hash, signature) // Vrifie la signature
    }
}

exports.KeyGenerator = KeyGenerator // Exporte la classe KeyGenerator
exports.SignatureGenerator = SignatureGenerator // Exporte la classe SignatureGenerator